import { Component, Prop, h } from '@stencil/core';

export type BadgeDotSize = 'sm' | 'md' | 'lg';
export type BadgeDotColor = 'success' | 'blue' | 'error' | 'warning';

@Component({
  tag: 'bh-badge-dot',
  styleUrl: 'bh-badge-dot.css',
  shadow: true,
})
export class BhBadgeDot {
  /**
   * The size of the badge dot
   */
  @Prop() size: BadgeDotSize = 'md';

  /**
   * The color of the badge dot
   */
  @Prop() color: BadgeDotColor = 'error';

  /**
   * Whether to show an outline ring around the dot
   */
  @Prop() outline: boolean = false;

  render() {
    const dotClasses = {
      'badge-dot': true,
      [`badge-dot-${this.size}`]: true,
      [`badge-dot-${this.color}`]: true,
      'badge-dot-outline': this.outline,
    };

    return <div class={dotClasses} part="dot"></div>;
  }
}
